import React, {Component} from 'react';
import { Modal, Form, Input, Upload, Icon, Button } from 'antd';
import { injectIntl, FormattedMessage, defineMessages } from 'react-intl';

const messages = defineMessages({
  selectFile: {
    id: 'common.selectFile',
    defaultMessage: '选择文件',
  },
});

class UploadFile extends Component {
  constructor(props) {
    super(props);

    const value = props.value || {};
    this.state = {
      file_name: value.file_name,
      file: value.file,
      uploading: false,
      fileList: value.file_name ? [{
        uid: '1',
        name: value.file_name,
        status: 'done',
        url: '',
      }]: []
    };
  }

  onRemove = (file) => {
    this.setState((state) => {
      const index = state.fileList.indexOf(file);
      const newFileList = state.fileList.slice();
      newFileList.splice(index, 1);
      return {
        fileList: newFileList,
      };
    });
    this.triggerChange({
      file_name: '',
      file: ''
    })
  };

  beforeUpload = (file) => {
    this.setState(state => ({
      fileList: [file],
    }));
    this.triggerChange({
      file_name: '',
      file,
    })
    return false;
  };

  triggerChange = (file)  => {
    const onChange = this.props.onChange;
    if (onChange) {
      onChange(Object.assign({}, file));
    }
  }

  render() {
    const isEdit = this.props.isEdit;
    return (
      <Upload fileList={this.state.fileList} onRemove={this.onRemove} beforeUpload={this.beforeUpload} accept=".apk">
        <Button><Icon type="upload" />
        <FormattedMessage {...messages.selectFile} />
        </Button>
      </Upload>
    )
  }
}

export default injectIntl(UploadFile)