import React from 'react';
import {
  Card,
  Form,
  Button,
  message,
  Popconfirm,
  Select,
  DatePicker,
} from 'antd';
import { injectIntl, FormattedMessage, defineMessages } from 'react-intl';

import StandardTable from '../../../components/StandardTable';
import ImageViewer from '../../../components/ImageViewer';
import ListHeader from '../../../components/ListHeader';
import DeviceUpdate from './DeviceUpdate';

import EditForm from './EditForm';

import {
  getVersionList,
  deleteVersion,
  addVersion,
  editVersion,
  getVersionInfo,
} from '../../../api/system'

import { defaultPageCount, fixUndefinedData, parseDate, getUUID, dateTimeToMoment,getDow, get_date} from '../../../utils/utils';
import { cancelApiRequsets } from '../../../utils/fetch';


import styles from './index.module.scss';

const FormItem = Form.Item;
const Option = Select.Option;


const messages = defineMessages({
  versionList: {
    id: 'device.versionList',
    defaultMessage: '终端版本列表',
  },
  versionNumber: {
    id: 'device.versionNumber',
    defaultMessage: '版本号',
  },
  fileName: {
    id: 'common.fileName',
    defaultMessage: '文件名',
  },
  description: {
    id: 'common.description',
    defaultMessage: '描述',
  },
  uploadTime: {
    id: 'common.uploadTime',
    defaultMessage: '上传时间'
  },
  failedToGetData: {
    id: 'common.error.failedToGetData',
    defaultMessage: '获取数据失败!'
  },
  delete: {
    id: 'common.delete',
    defaultMessage: '删除',
  },
  deleteConfirm: {
    id: 'common.delete.confirm',
    defaultMessage: '确定删除吗?'
  },
  deleteSuccess: {
    id: 'common.delete.success',
    defaultMessage: '删除成功!'
  },
  deleteError: {
    id: 'common.delete.error',
    defaultMessage: '删除失败!'
  },
  edit: {
    id: 'common.edit',
    defaultMessage: '编辑',
  },
  operate: {
    id: 'common.operate',
    defaultMessage: '操作'
  },
  add: {
    id: 'common.add',
    defaultMessage: '添加',
  },
  updateDevice: {
    id: 'device.updateDevice',
    defaultMessage: '设备更新',
  },
  addVersion: {
    id: 'device.addVersion',
    defaultMessage: '版本添加'
  },
  updateVersion: {
    id: 'device.updateVersion',
    defaultMessage: '版本更新'
  },
  success: {
    id: 'common.success',
    defaultMessage: '成功!'
  },
  failed: {
    id: 'common.failed',
    defaultMessage: '失败了!'
  },
})
class SystemManage extends React.Component {
  columns = [
    {
      title: <FormattedMessage {...messages.versionNumber}/>,
      dataIndex: 'version_number',
      render: (text, record) => (
        <React.Fragment>
          <span className={styles.wordBreak}>{text}</span>
        </React.Fragment>
      )
    },
    {
      title: <FormattedMessage {...messages.fileName}/>,
      dataIndex: 'file_name',
      render: (text, record) => (
        <React.Fragment>
          <span className={styles.wordBreak}>{text}</span>
        </React.Fragment>
      )
    },
    {
      title: <FormattedMessage {...messages.description}/>,
      dataIndex: 'description',
      render: (text, record) => (
        <React.Fragment>
          <span className={styles.wordBreak}>{text}</span>
        </React.Fragment>
      )
    },
    {
      title: <FormattedMessage {...messages.uploadTime}/>,
      dataIndex: 'upload_time',
      render: (text, record) =>
        <React.Fragment>
          {parseDate(text)}
        </React.Fragment>
    },
    {
      title: <FormattedMessage {...messages.operate} />,
      dataIndex: 'operate',
      render: (text, record) => (
        <React.Fragment>
          <a href="javascript:;" onClick={() =>{this.showUpdateModal(record.version_id)}} className={styles.operatorLink}>
            <FormattedMessage {...messages.updateDevice} />
          </a>
          <a href="javascript:;" onClick={() =>this.showEditModal(record.version_id)} className={styles.operatorLink}>
            <FormattedMessage {...messages.edit} />
          </a>
          <Popconfirm
            className={styles.operatorLink}
            title={<FormattedMessage {...messages.deleteConfirm} />}
            onConfirm={() => this.onDelete(record.version_id)}
          >
            <a href="javascript:;"><FormattedMessage {...messages.delete} /></a>
          </Popconfirm>
        </React.Fragment>
      )
    }
  ];
  queryStrings = this.props.location.query || {}

  state = {
    selectedRows: [],
    loading: false,
    confirmLoading: false,
    locked: false,
    visible: false,
    isUpdateVisible: false, 
    acountOptions: [],
    versionList: [],
    formData: {},
    // 表格搜索框表单
    search_form: {},
    uuid: Date.now(),
    updateKey: getUUID(),
    operateId: '',
    total: 0,
    page_number: 1,
    page_size: defaultPageCount,
    batchDeleteMode: false,
  };

  componentDidMount() {
    this._loadAsyncData();
  }

  componentWillUnmount() {
    cancelApiRequsets();
  }

  showModal = () => {
    let formData = {
      title: <FormattedMessage {...messages.updateDevice} />
    };
    this.setState({ visible: true, formData });
  };

  showUpdateModal = (id) => {
    this.setState({
      isUpdateVisible: true,
      operateId: id,
      updateKey: getUUID(),
    })
  }

  showEditModal = (id) => {
    if (this.state.locked) {
      return;
    }
    this.setState({
      locked: true
    })
    getVersionInfo(id).then((res) => {
      if (res.code === 1000) {
        const {version_id, file_name, description, version_number} = res.data
        let formData = {
          title:  <FormattedMessage {...messages.updateVersion} />,
          version_id,
          version_file: { file_name },
          description,
          version_number,
        };
        this.setState({ visible: true, formData });
      } else {
        message.error(res.msg || this.props.intl.formatMessage(messages.failedToGetData));
      }
      this.setState({
        locked: false
      })
    }).catch(e => {
      message.error(this.props.intl.formatMessage(messages.failedToGetData))
      console.log(e)
      this.setState({
        locked: false
      })
    });
  }

  _loadAsyncData() {
    this.setState({
      loading: true
    });

    let { page_number, page_size, search_form = {} } = this.state;
    let versionList = [];

    getVersionList({ page_number, page_size, ...search_form })
      .then(response => {
        let data = response.data;
        if (response.code === 1000) {
          versionList = data.versionList || [];
          let total = data.total_number || 0;
          this.setState({
            versionList,
            total,
            selectedRows: []
          });
        } else {
          this.setState({
            versionList: [],
            selectedRows: []
          });
          message.error(response.msg || this.props.intl.formatMessage(messages.failedToGetData));
        }
      })
      .catch(e => {
        console.log(e);
      })
      .finally(() => {
        this.setState({
          loading: false
        });
      });
  }

  handleModal = (stateProps, flag) => {
    this.setState({
      [stateProps]: flag
    })
  }

  /**
   * 处理分页变化
   */
  handleStandardTableChange = (pagination, filtersArg, sorter) => {
    let { page_number, page_size } = this.state;

    if (page_size !== pagination.pageSize) {
      // page size changed, then reset page number to 1
      page_number = 1;
    } else {
      page_number = pagination.current;
    }

    page_size = pagination.pageSize;

    this.setState(
      {
        page_number,
        page_size
      },
      this._loadAsyncData
    );
  };

    /**
   * 删除子账号
   * @param {Number} id 子账号ID
   */
  onDelete = id => {
    deleteVersion(id).then((res) => {
      if (res.code === 1000) {
        message.success(this.props.intl.formatMessage(messages.deleteSuccess));
        this._loadAsyncData();
      } else {
        message.error(res.msg || this.props.intl.formatMessage(messages.deleteError));
      }
    })
    .catch(e => {
      message.error(this.props.intl.formatMessage(messages.deleteError));
      console.log(e)
    });
  }

  /**
   * 处理表格选择事件
   * @param {Array[Object]} 已选择的列表项
   */
  handleSelectRows = rows => {
    this.setState({
      selectedRows: rows
    });
  };

  /**
   * 设置ref
   * @param {Object} formRef form组件
   */
  saveFormRef = formRef => {
    this.formRef = formRef;
  };

  /**
   * 关闭 添加/编辑 Modal
   */
  handleCancel = () => {
    this.setState({ visible: false, uuid: Date.now() });
  };

  handleCreate = () => {
    const form = this.formRef.props.form;
    form.validateFields((err, values) => {
      if (err) {
        return;
      }
      const  { version_id, version_number, version_file = {}, description } = values;
      const data = fixUndefinedData({
        version_id,
        version_number,
        version_file: version_file.file,
        description,
      });
      let method = addVersion;
      let text = this.props.intl.formatMessage(messages.addVersion);
      let successText = this.props.intl.formatMessage(messages.success);
      let failedText = this.props.intl.formatMessage(messages.failed)
      if (data.version_id) {
        method = editVersion;
        text = this.props.intl.formatMessage(messages.updateVersion);
      }
      this.setState({
        confirmLoading: true,
      })
      method(data).then(res => {
        if (res.code === 1000) {
          message.success(`${text}${successText}`);
          this._loadAsyncData();
          form.resetFields();
          this.setState({ visible: false });
        } else {
          message.error(res.msg || `${text}${failedText}`);
        }
        this.setState({
          confirmLoading: false,
        })
      }).catch(e => {
        this.setState({
          confirmLoading: false,
        })
        message.error(`${text}${failedText}`);
        console.log(e)
      });
    });
  };

  render() {
    const {
      loading,
      selectedRows,
      versionList,
      total,
      page_number,
      page_size,
      visible,
      formData,
      confirmLoading,
      uuid,
    } = this.state;

    const pagination = {
      current: page_number,
      pageSize: page_size,
      total,
    };

    return (
      <React.Fragment>

        <Card bordered={false}>
          <div className={styles.tableList}>
            <ListHeader
              title={<FormattedMessage {...messages.versionList}/>}
              buttonText={<FormattedMessage {...messages.add}/>}
              doAction={this.showModal}
              style={{ marginBottom: 5 }}
          />

            <StandardTable
              rowKey="version_id"
              selectedRows={selectedRows}
              selectable={false}
              pagination={pagination}
              loading={loading}
              showAlert={false}
              dataList={versionList}
              columns={this.columns}
              onSelectRow={this.handleSelectRows}
              onChange={this.handleStandardTableChange}
            />

            <EditForm
              wrappedComponentRef={this.saveFormRef}
              visible={visible}
              onCancel={this.handleCancel}
              onCreate={this.handleCreate}
              formData={formData}
              key={uuid}
              confirmLoading={confirmLoading}
            />

            <DeviceUpdate 
              visible={this.state.isUpdateVisible}
              key={this.state.updateKey}
              onCancel={() => this.handleModal('isUpdateVisible', false)}
              id={this.state.operateId}
              title={this.props.intl.formatMessage(messages.updateDevice)}
            />
          </div>
        </Card>

      </React.Fragment>
    );
  }
}

export default injectIntl(Form.create()(SystemManage));
