import React, {Component} from 'react';
import { injectIntl, FormattedMessage, defineMessages } from 'react-intl';
import {Spin} from 'antd'
import Lightbox from 'react-image-lightbox'
import 'react-image-lightbox/style.css';

import styles from './index.module.css';

const messages = defineMessages({
  loadFail: {
    id: 'component.ImageViewer.loadFail',
    defaultMessage: '图片加载失败!'
  },
});

const fixWrap = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)'
}

class ImageViewer extends Component {
  constructor(props) {
    super(props);

    this.state = {
      photoIndex: 0,
    };
  }

 render() {
  const {isOpen, imageUrl, onClose, isLoading, imagePadding} = this.props
   return (
    <div>
      {isOpen && <div>
        {imageUrl && (!isLoading) &&(<Lightbox
          mainSrc={imageUrl}
          onCloseRequest={onClose}
          enableZoom={false}
          imageLoadErrorMessage={<FormattedMessage {...messages.loadFail} />}
          wrapperClassName={styles.imageViewer}
          imagePadding={imagePadding}
          imageTitle="" />)}
        {isLoading && <Spin style={fixWrap}/> }
      </div>}
    </div>
   )
 }
}

ImageViewer.defaultProps = {
  imagePadding: 100
}

export default injectIntl(ImageViewer)
