
import React from 'react';
import { message, Select, Cascader } from 'antd';
import { injectIntl, FormattedMessage, defineMessages } from 'react-intl';
import { loadState } from '../../utils/localStorage';
import {getAccounts} from '../../api/account';

const Option = Select.Option;

const messages = defineMessages({
  all: {
    id: 'common.all',
    defaultMessage: '全部'
  },
  failedToGetData: {
    id: 'common.error.failedToGetData',
    defaultMessage: '获取数据失败!'
  },
  select: {
    id: 'common.select',
    defaultMessage: '请选择'
  }
});

class SelectAccount extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      acountOptions: [],
    };

  }

  componentDidMount() {
    const useInfo = loadState('userInfo', true) || {}
    const loginAccount = useInfo.account_id;
    // 如果初始值是空，触发change，赋初值为登录用户
    this.initCascaderOptions(loginAccount);
  }


  loadSubAccounts = (selectedOptions) => {
    const targetOption = selectedOptions[selectedOptions.length - 1];
    const parent_id = targetOption.account_id
    targetOption.loading = true;
    this.getAccountList(parent_id).then((data) => {
      targetOption.loading = false;
      let accounts = data.accounts;
      accounts = accounts.map((it) => {
        return it;
      })
      targetOption.children = accounts;
      this.setState({
        acountOptions: [...this.state.acountOptions],
      });
    })
  }

  getAccountList(id) {
    const data = {
      account_id: id,
      page_size: 99999,
    }
    return new Promise((resolve, reject) => {
      getAccounts(data).then((response) => {
        if (response.code === 1000) {
          resolve(response.data)
        } else {
          reject(response.msg || this.props.intl.formatMessage(messages.failedToGetData));
        }
      })
    })

  }

  triggerChange = (value, option)  => {
    const onChange = this.props.onChange;
    if (onChange) {
      onChange(value, option);
    }
  }

  initCascaderOptions = (loginAccount) => {
    const useInfo = loadState('userInfo', true) || {}
    let root = {
      account_name: useInfo.account_name,
      account_id: useInfo.account_id,
      isLeaf: useInfo.role.role_id === 3,
      children: [],
    }

    this.getAccountList().then((data) => {
      let accounts = data.accounts || []
      accounts = accounts.map((it) => {
        // isLeaf : 是否是叶子
        it.isLeaf = it.role.role_id === 3
        if (this.props.value.length === 3 && it.account_id === this.props.value[1]) {
          this.getAccountList(it.account_id).then(data => {
            it.children = data.accounts.map((sub) => {
              sub.isLeaf = true;
              return sub;
            })
            this.setState({
              acountOptions: [root],
            })
          })
        } 
        return it
      })
      root.children = accounts;
      this.setState({
        acountOptions: [root],
      })
    }).catch(err => {
      message.error(err)
    })
  }

  render() {
    return (

    <Cascader
    value={this.props.value || []}
    options={this.state.acountOptions}
    style={this.props.style}
    onChange={this.triggerChange}
    loadData={this.loadSubAccounts}
    changeOnSelect
    placeholder={this.props.intl.formatMessage(messages.select)}
    fieldNames={{ label: 'account_name', value: 'account_id', children: 'children' }}/>
    );
  }
}

export default injectIntl(SelectAccount);
