import moment from 'moment';
import qs from 'querystringify';
import { getStorageServerAddress } from './address'

export const numbers = length =>
  Array.apply(null, { length: length }).map(Number.call, Number);

// type: "today","three","week"
export const getDate = type => {
  switch (type) {
    case 'today':
      let today = [
        moment()
          .startOf('day')
          .format('YYYY-MM-DD HH:mm:ss'),
        moment()
          .endOf('day')
          .format('YYYY-MM-DD HH:mm:ss')
      ];
      return today;
    case 'three':
      let three = [
        moment()
          .subtract(2, 'day')
          .startOf('day')
          .format('YYYY-MM-DD HH:mm:ss'),
        moment()
          .endOf('day')
          .format('YYYY-MM-DD HH:mm:ss')
      ];
      return three;
    case 'week':
      let week = [
        moment()
          .subtract(6, 'day')
          .startOf('day')
          .format('YYYY-MM-DD HH:mm:ss'),
        moment()
          .endOf('day')
          .format('YYYY-MM-DD HH:mm:ss')
      ];
      return week;
    default:
      break;
  }
};

export const parseDate = strDate => {
  if (strDate) {
    return moment(strDate).format('YYYY-MM-DD HH:mm:ss');
  }

  return null;

};

/**
 * 时间字符串转moment
 * @param {*} strDateTime 
 */
export const dateTimeToMoment = strDateTime => {
  if (strDateTime) {
    return moment(strDateTime)
  }
}

export const parseDateTime = strDate => {
  if (strDate) {
    return moment(strDate, 'YYYY-MM-DD HH:mm:ss').valueOf()
  }

  return null;

};

export const get_date = strDate => {
  if (strDate) {
    return moment(strDate).format('YYYY-MM-DD');
  }

  return null;

};

export const get_time = strDate => {
  if (strDate) {
    return moment(strDate).format('HH:mm:ss');
  }

  return null;

};

export const conbineNames = (arr, keys, keyProp, valueProp) => {
  let conbinedNames = arr.reduce((allNames, current) => {

    keys.forEach(key => {
      if (current[keyProp] === key) {
        allNames += ',' + current[valueProp];
      }
    });

    return allNames;
  }, '');

  if (conbinedNames.length > 0) {
    conbinedNames = conbinedNames.substring(1)
  }

  return conbinedNames;
};

export function getPhotoUrl(photos) {
  if (photos) {
    let baseUrl = getStorageServerAddress()
    let photoObj = {}
    if (photos instanceof Array) {
      photoObj = photos[0] || {}
    } else {
      photoObj = photos || {}
    }

    let fullName = baseUrl + "/" + photoObj.photo_url
    return fullName;

  }

  return ""

}

/**
 *非必填项未填，则删除该字段
 *
 * @export
 * @param {Object} obj 要处理的表单
 * @returns 整理后的表单
 */
export function fixUndefinedData(obj) {
  const keys = Object.keys(obj)
  return keys.reduce((res, item) => {
    if (typeof obj[item] !== 'undefined') {
      res[item] = obj[item]
    }
    return res
  }, {})
}

export function getValueFromQuery(querystring, key) {
  if (!(querystring && key)) {
    return ''
  }
  return qs.parse(querystring)[key]
}

export function parseQuery(querystring) {
  if (!querystring) {
    return {}
  }
  return qs.parse(querystring)
}

export function getDow() {
  return moment(Date.now())
}

export function getUUID() {
  return Date.now() + Math.random()
}

export function base64Prefix(dataUrl) {
  if (dataUrl.split(',').length === 1) {
    dataUrl = `data:image/jpg;base64,${dataUrl}`
  }
  return dataUrl
}

// 分页
export const pageSizeOptions = ['30', '50', '80', '100']

export const defaultPageCount = 30

export const paginationOption = {
  showSizeChanger: true,
  showQuickJumper: true,
  defaultPageSize: defaultPageCount,
}


// 正则

export const IDCARD_REG = /^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/i

export const PHONE_REG = /^\d{6,20}$/

export const IP_REG = /(?=(\b|\D))(((\d{1,2})|(1\d{1,2})|(2[0-4]\d)|(25[0-5]))\.){3}((\d{1,2})|(1\d{1,2})|(2[0-4]\d)|(25[0-5]))(?=(\b|\D))/

export const URL_REG = /(https?):\/\/[-A-Za-z0-9+&@#/%?=~_|!:,.;]+[-A-Za-z0-9+&@#/%=~_|]/i

export const MAC_REG = /^[A-F0-9]{2}(-[A-F0-9]{2}){5}$|^[A-F0-9]{2}(:[A-F0-9]{2}){5}$/i

export const IPPORT_REG = /(?=(\b|\D))(((\d{1,2})|(1\d{1,2})|(2[0-4]\d)|(25[0-5]))\.){3}((\d{1,2})|(1\d{1,2})|(2[0-4]\d)|(25[0-5]))(?=(\b|\D)):\d{1,5}/

export const LETTER_NUM_REG = /^[0-9a-z]+$/i