import React from 'react';
import { Button, Divider } from 'antd';
import styles from './index.module.css';

export default ({title,Title, buttonText,doAction, buttonDisabled,buttonHide,hasDivider,customButton,buttonType, ...rest}) => {
  let btnType = buttonType || 'primary'
  const button = (customButton && (<div>{customButton}</div>)) ||
    <Button type={btnType} onClick={doAction} disabled={buttonDisabled}>{buttonText}</Button>
  return (
  <div style={{ marginBottom: 5 }} {...rest}>
    <header className={styles.total_header}>
      <div>{Title ? <Title /> : title}</div>
      {(!buttonHide) && button}
    </header>
    {hasDivider && <Divider className={styles.mbt10_5} />}
  </div>
)};
