import fetch from '../utils/fetch'

const apis = {
  list: '/api/system/versionList',
  version: '/api/system/version',
  update: '/api/system/versionUpdate'
}

export function getVersionList(data) {
  return fetch.get(apis.list, {
    params: {
      ...data,
    }
  });
}

export function deleteVersion(id) {
  return fetch.delete(apis.version, {
    data: {
      version_id: id
    }
  });
}

export function addVersion(model) {
  var formData = new FormData();
  var data = commonFormData(formData, model);
  return fetch.post(apis.version, data);
}

export function editVersion(model) {
  var formData = new FormData();
  var data = commonFormData(formData, model);
  return fetch.put(apis.version, data);
}

export function getVersionInfo(id) {
  return fetch.get(apis.version, {
    params: {
      version_id: id,
    }
  })
}

export function updateVersion(id, equipmentIds) {
  return fetch.post(apis.update, {
    equipment_ids: equipmentIds,
    version_id: id,
  })
}

/**
 *遍历对象添加到formData中
 *
 * @param {FormData} formData
 * @param {Object} model
 * @returns {FormData} formData
 */
function commonFormData(formData, model) {
  const keys = Object.keys(model)
  keys.forEach((it) => {
    formData.append(it, model[it])
  })
  return formData;
}