// 暂不需要
import React from 'react';
import { Modal, message, Badge, Form, Select,Row,Col,Input,Button, Table} from 'antd';
import { injectIntl, FormattedMessage, defineMessages } from 'react-intl';

import ListHeader from '../../../components/ListHeader';
import SelectAccount from '../../../components/SelectAccount';
import { cancelApiRequsets } from '../../../utils/fetch';
import { getEquipments } from '../../../api/equipment';
import { updateVersion } from '../../../api/system';

import { loadState } from '../../../utils/localStorage';

import maxLength from '../../../utils/maxLength'

import styles from './index.module.scss';

const FormItem = Form.Item;
const Option = Select.Option;

const messages = defineMessages({
  total: {
    id: 'common.total',
    defaultMessage: '总计'
  },
  update: {
    id: 'common.update',
    defaultMessage: '更新'
  },
  updateSuccess: {
    id: 'common.update.success',
    defaultMessage: '更新成功!'
  },
  updateError: {
    id: 'common.update.error',
    defaultMessage: '更新失败了!'
  },
  items: {
    id: 'common.items',
    defaultMessage: '项'
  },
  search: {
    id: 'common.search',
    defaultMessage: '查询',
  },
  reset: {
    id: 'common.reset',
    defaultMessage: '重置'
  },
  failedToGetData: {
    id: 'common.error.failedToGetData',
    defaultMessage: '获取数据失败!'
  },
  deviceList: {
    id: 'device.deviceList',
    defaultMessage: '设备列表',
  },
  deviceName: {
    id: 'device.deviceName',
    defaultMessage: '设备名称',
  },
  deviceID: {
    id: 'device.deviceID',
    defaultMessage: '设备ID',
  },
  connectStatus: {
    id: 'device.connectStatus',
    defaultMessage: '连接状态'
  },
  sourceAccount: {
    id: 'account.sourceAccount',
    defaultMessage: '所属账户'
  },
  all: {
    id: 'common.all',
    defaultMessage: '全部'
  },
  online: {
    id: 'common.online',
    defaultMessage: '在线'
  },
  offline: {
    id: 'common.offline',
    defaultMessage: '离线'
  },
  sysVersion: {
    id: 'device.sysVersion',
    defaultMessage: '系統版本',
  },
  selectedRowRequired: {
    id: 'common.selectedRow.required',
    defaultMessage: '请先选择要操作的数据!'
  }
})

class ViewForm extends React.Component {
  state = {
    loading: false,
    locked: false,
    dataList: [],
    // 表格搜索框表单
    search_form: {
      account_id: this.accountId || ''
    },
    total: 0,
    page_number: 1,
    page_size: 10,
    selectedRows: [],
  }

  columns = [
    {
      title: <FormattedMessage {...messages.deviceName} />,
      dataIndex: 'equipment_name',
      render: (text, record) => (
        <React.Fragment>
          <span className={styles.wordBreak}>{text}</span>
        </React.Fragment>
      ),
      width: 150,
    },
    {
      title: <FormattedMessage {...messages.deviceID} />,
      dataIndex: 'equipment_id',
      width: 100,
    },
    {
      title: <FormattedMessage {...messages.sysVersion} />,
      dataIndex: 'sysVersion',
      render: (text, record) => (
        <React.Fragment>
          <span className={styles.wordBreak}>{text}</span>
        </React.Fragment>
      ),
      width: 150,
    },
    {
      title:  <FormattedMessage {...messages.connectStatus} />,
      dataIndex: 'status',
      render: (text,record) => (
        <React.Fragment>
          <Badge status={['success','error'][text - 1]} />
          {[<FormattedMessage {...messages.online} />, <FormattedMessage {...messages.offline} />][text-1]}
        </React.Fragment>
      ),
      width: 100,
    },
    {
      title:  <FormattedMessage {...messages.sourceAccount} />,
      dataIndex: 'account_name',
      render: (text, record) => (
        <React.Fragment>
          <span className={styles.wordBreak}>{text}</span>
        </React.Fragment>
      ),
      width: 150,
    },
  ];

  componentDidMount() {
    this._loadAsyncData();
    const initAccountIds = this.getInitAccountIds();
    const form = this.props.form;
    const accountId =initAccountIds.slice(-1)[0]
    this.setState({
      search_form: {
        account_ids: initAccountIds,
        account_id: accountId,
      }
    });
    form.setFieldsValue({
      account_ids: initAccountIds,
      account_id: accountId,
    }, () => {
      this._loadAsyncData();
    })
  }

  onAccountChange = (value) => {
    const form = this.props.form
    form.setFieldsValue({
      account_id: value[value.length - 1]
    })
  }

  getInitAccountIds = () => {
    const userInfo = loadState('userInfo', true);
    const role = userInfo.role;
    const account_id = userInfo.account_id;
    let res = [account_id];
    // 当前是admin,连接中存在accountId,说明是admin查看子账号的人数/设备数
    if (role === 1 && this.accountId) {
      if (this.parentId) {
        res.push(Number(this.parentId))
      }
      res.push(this.accountId)
    }
    return res
  }

  componentWillUnmount() {
    cancelApiRequsets();
  }

  _loadAsyncData = () => {
    if(!this.props.id) {
      return
    }
    this.setState({
      loading:true,
    })

    let dataList = []
    let { page_number, page_size, search_form = {} } = this.state;

    getEquipments({page_number, page_size, ...search_form}).then((res) => {
      let data = res.data;
      if (res.code === 1000) {
        dataList = data.equipments || [];
        let total = data.total_number || 0;
        this.setState({
          dataList,
          total,
        });
      } else {
        this.setState({
          dataList: []
        })
        message.error(res.msg || this.props.intl.formatMessage(messages.failedToGetData))
      }
    }).catch((e) => {
      message.error(this.props.intl.formatMessage(messages.failedToGetData))
    }).finally(() => {
      this.setState({
        loading: false
      });
    });
  }

  doUpdate = () => {
    const equipmentIds = this.state.selectedRows;
    const versionId = this.props.id;
    if (!equipmentIds.length) {
      message.warning(this.props.intl.formatMessage(messages.selectedRowRequired))
      return
    }
    if (this.state.locked) {
      return
    }
    this.setState({
      locked: true
    })
    updateVersion(versionId, equipmentIds).then(res => {
      if(res.code === 1000) {
        message.success(this.props.intl.formatMessage(messages.updateSuccess))
        // 取消勾选已操作的项
        this.setState({
          selectedRows: []
        })
      } else {
        message.error(res.msg || this.props.intl.formatMessage(messages.failedToGetData))
      }
      this.setState({
        locked: false
      })
    }).catch(e => {
      message.error(this.props.intl.formatMessage(messages.updateError))
      this.setState({
        locked: false
      })
    })

  }

  /**
   * 处理页码变化事件
   * @param {Object} pagination 分页配置
   */
  handleStandardTableChange = (pagination, filtersArg, sorter) => {
    let { page_number, page_size } = this.state;

    if (page_size !== pagination.pageSize) {
      // page size changed, then reset page number to 1
      page_number = 1;
    } else {
      page_number = pagination.current;
    }

    page_size = pagination.pageSize;

    this.setState(
      {
        page_number,
        page_size
      },
      this._loadAsyncData
    );
  };

  /**
   * 渲染表格搜索框
   */
  renderSimpleForm() {
    const {
      form: { getFieldDecorator }
    } = this.props;

    
    return (
      <Form onSubmit={this.handleSearch} layout="inline" style={{display: 'flex', padding: '5px', alignItems: 'flex-end', justifyContent: 'space-between'}}>
        <div>
          {(loadState('userInfo', true) || {}).role !== 3 && 
          <React.Fragment>
            {getFieldDecorator('account_id')(<Input type="hidden"/>)}
            <FormItem label={<FormattedMessage {...messages.sourceAccount}/>}>
              {getFieldDecorator('account_ids')(
                <SelectAccount onChange={this.onAccountChange}/>
              )}
            </FormItem>
            </React.Fragment>}

            <FormItem label={<FormattedMessage {...messages.deviceName} />}>
              {getFieldDecorator('equipment_name')(
                <Input maxLength={maxLength.deviceName}/>
              )}
            </FormItem>

            <FormItem label={<FormattedMessage {...messages.sysVersion} />}>
              {getFieldDecorator('version_name')(
                <Input maxLength={maxLength.versionName}/>
              )}
            </FormItem>
          </div>
          <Button type="primary" htmlType="submit"><FormattedMessage  {...messages.search}/></Button>
      </Form>
    );
  }

  /**
   * 处理表格选择事件
   * @param {Array[Object]} 已选择的列表项
   */
  handleSelectRows = rows => {
    this.setState({
      selectedRows: rows
    });
  };

    /**
   * 点击搜索
   * @param {Object} e 事件对象
   */
  handleSearch = e => {
    e.preventDefault();

    const { form } = this.props;

    form.validateFields((err, fieldsValue) => {
      if (err) return;
      const searchData = {
        ...fieldsValue,
      }
      this.setState(
        {
          page_number: 1,
          search_form: searchData
        },
        this._loadAsyncData
      );
    });
  };
  

  render() {
    const {
      visible,
      onCancel,
      id,
      title,
    } = this.props;
    
    const {
      selectedRows,
      loading,
      dataList,
      page_number,
      page_size,
      total,
    } = this.state

    const pagination = {
      current: page_number,
      pageSize: page_size,
      showQuickJumper: true,
      total,
    };

    const rowSelection = {
      onChange: this.handleSelectRows,
      selectedRowKeys: selectedRows,
      getCheckboxProps: record => ({
        disabled: record.disabled,
      }),
    };

    return (
      <Modal
        visible={visible}
        title={title}
        onCancel={onCancel}
        width={980}
        footer={null}
        destroyOnClose={true}
      >
      <div className={styles.tableList}>
        <div>
          {this.renderSimpleForm()}
        </div>

        <ListHeader
            title=""
            buttonText={<FormattedMessage {...messages.update}/>}
            buttonType="default"
            doAction={this.doUpdate}
            style={{ marginBottom: 5, marginTop: 20 }}
          />

        <Table
              rowKey="equipment_id"
              pagination={pagination}
              loading={loading}
              dataSource={dataList}
              columns={this.columns}
              rowSelection={rowSelection}
              scroll={{y: 400}}
              onChange={this.handleStandardTableChange}
            />
      </div>
      </Modal>
    );
  }
}

export default injectIntl(Form.create()(ViewForm));
