import fetch from '../utils/fetch'

const apis = {
  list: '/api/equipment/list',
  equipment: '/api/equipment',
  openDoor: '/api/equipment/open',
  closeDoor: '/api/equipment/close',
  resetPwd: '/api/equipment/reset',
  sync: '/api/equipment/synchronization',
  preview: '/api/equipment/preview',
}

/**
 *获取设备列表
 *
 * @export
 * @param {Object} {
 *   equipment_name
 * }
 * @returns
 */
export function getEquipments(data) {
  return fetch.get(apis.list, {
    params: {
      ...data,
    }
  });
}

/**
 *添加设备
 *
 * @export
 * @param {Object} data
 * @returns
 */
export function addEquipment(data) {
  return fetch.post(apis.equipment, data);
}

/**
 *更新设备
 *
 * @export
 * @param {Object} data
 * @returns
 */
export function updateEquipment(data) {
  return fetch.put(apis.equipment, data);
}

/**
 *删除设备
 *
 * @export
 * @param {Number} id
 * @returns
 */
export function deleteEquipment(id) {
  return fetch.delete(apis.equipment, {
    data: {
      equipment_id: id
    }
  });
}


/**
 *获取设备详情
 *
 * @export
 * @param {Number} id
 * @returns
 */
export function getEquipmentInfo(id) {
  return fetch.get(apis.equipment,{
    params: {
      equipment_id: id,
    }
  });
}



/**
 * 紧急常开
 *
 * @export
 * @param {Number} id
 * @returns
 */
export function openDoor(id) {
  return fetch.post(apis.openDoor, {
    equipment_id: id
  });
}

/**
 * 取消常开
 *
 * @export
 * @param {Number} id
 * @returns
 */
export function closeDoor(id) {
  return fetch.post(apis.closeDoor, {
    equipment_id: id
  });
}



/**
 *重置密码
 *
 * @export
 * @param {Number} id
 * @returns
 */
export function resetPwd(id) {
  return fetch.post(apis.resetPwd, {
    equipment_id: id
  })
}

export function syncPeople(id) {
  return fetch.post(apis.sync, {
    equipment_id: id
  })
}

export function previewDevice(id) {
  return fetch.get(apis.preview, {
    params: {
      equipment_id: id
    }
  })
}