import React, { PureComponent, Fragment } from 'react';
import { Table, Alert } from 'antd';
import { injectIntl, FormattedMessage, defineMessages } from 'react-intl';
import styles from './index.module.scss';

function initTotalList(columns) {
  const totalList = [];
  columns.forEach(column => {
    if (column.needTotal) {
      totalList.push({ ...column, total: 0 });
    }
  });
  return totalList;
}

const messages = defineMessages({
  total: {
    id: 'common.total',
    defaultMessage: '总计',
  },
  clear: {
    id: 'common.clear',
    defaultMessage: '清空',
  },
  selected: {
    id: 'common.selected',
    defaultMessage: '请选择',
  },
  items: {
    id: 'common.items',
    defaultMessage: '项',
  }
});

class StandardTable extends PureComponent {
  constructor(props) {
    super(props);
    const { columns } = props;
    const needTotalList = initTotalList(columns);

    this.state = {
      selectedRowKeys: [],
      needTotalList,
    };
  }

  static getDerivedStateFromProps(nextProps) {
    // clean state
    if ((nextProps.selectedRows || []).length === 0) {
      const needTotalList = initTotalList(nextProps.columns);
      return {
        selectedRowKeys: [],
        needTotalList,
      };
    }
    return null;
  }

  handleRowSelectChange = (selectedRowKeys, selectedRows) => {
    let { needTotalList } = this.state;
    needTotalList = needTotalList.map(item => ({
      ...item,
      total: selectedRows.reduce((sum, val) => sum + parseFloat(val[item.dataIndex], 10), 0),
    }));
    const { onSelectRow } = this.props;
    if (onSelectRow) {
      onSelectRow(selectedRows);
    }

    this.setState({ selectedRowKeys, needTotalList });
  };

  handleTableChange = (pagination, filters, sorter) => {
    const { onChange } = this.props;
    if (onChange) {
      onChange(pagination, filters, sorter);
    }
  };

  cleanSelectedKeys = () => {
    this.handleRowSelectChange([], []);
  };

  render() {
    const { selectedRowKeys, needTotalList } = this.state;
    const {
      dataList,
      pagination,
      loading,
      columns,
      rowKey,
      selectable,
      showAlert,
    } = this.props;

    const paginationProps = {
      showSizeChanger: true,
      showQuickJumper: true,
      showTotal: total => `${this.props.intl.formatMessage(messages.total)} ${total} ${this.props.intl.formatMessage(messages.items)}`,
      ...pagination,
    };
    const rowSelection = {
      selectedRowKeys,
      onChange: this.handleRowSelectChange,
      getCheckboxProps: record => ({
        disabled: record.disabled,
      }),
    };

    return (
      <div className={styles.standardTable}>
        {selectable && showAlert && <div className={styles.tableAlert}>
          <Alert
            message={
              <Fragment>
                <FormattedMessage {...messages.selected} /> <a style={{ fontWeight: 600 }}>{selectedRowKeys.length}</a> <FormattedMessage {...messages.items} />&nbsp;&nbsp;
                {needTotalList.map(item => (
                  <span style={{ marginLeft: 8 }} key={item.dataIndex}>
                    {item.title}
                    <FormattedMessage {...messages.total} />&nbsp;
                    <span style={{ fontWeight: 600 }}>
                      {item.render ? item.render(item.total) : item.total}
                    </span>
                  </span>
                ))}
                <a onClick={this.cleanSelectedKeys} style={{ marginLeft: 24 }}>
                <FormattedMessage {...messages.clear} />
                </a>
              </Fragment>
            }
            type="info"
            showIcon
          />
        </div>}
        <Table
          loading={loading}
          rowKey={rowKey || 'key'}
          rowSelection={selectable ? rowSelection : null}
          dataSource={dataList}
          columns={columns}
          pagination={paginationProps}
          onChange={this.handleTableChange}
        />
      </div>
    );
  }
}

StandardTable.defaultProps = {
  selectable: true,
  showAlert: true,
}

export default injectIntl(StandardTable);
