import React from 'react';
import { Modal, Form, Input, Upload, Icon, Button } from 'antd';
import { injectIntl, FormattedMessage, defineMessages } from 'react-intl';

import UploadFile from '../../../components/UploadFile';

import maxLength from '../../../utils/maxLength'

const messages = defineMessages({
  versionNumber: {
    id: 'device.versionNumber',
    defaultMessage: '版本号',
  },
  versionNumberRequired: {
    id: 'device.versionNumber.required',
    defaultMessage: '请输入版本号!'
  },
  file: {
    id: 'common.file',
    defaultMessage: '文件',
  },
  selectFile: {
    id: 'common.selectFile',
    defaultMessage: '选择文件',
  },
  fileRequired: {
    id: 'common.file.required',
    defaultMessage: '请选择文件!',
  },
  description: {
    id: 'common.description',
    defaultMessage: '描述'
  },
});

const FormItem = Form.Item;

const EditForm = Form.create()(
  class extends React.Component {

    checkFile =(rule, value, callback) => {
      if (value && ((!value.file) && (!value.file_name))) {
        callback(this.props.intl.formatMessage(messages.fileRequired));
      } else {
        callback();
      }
    };

    render() {
      const {
        visible,
        onCancel,
        onCreate,
        form,
        formData,
        confirmLoading,
      } = this.props;
      
      const { getFieldDecorator } = form;

      let model = formData || {};

      const formItemLayout = {
        labelCol: {
          xs: { span: 24 },
          sm: { span: 6 }
        },
        wrapperCol: {
          xs: { span: 24 },
          sm: { span: 18 }
        }
      };
      const isEdit = !!model.version_id;

      return (
        <Modal
          visible={visible}
          title={model.title}
          onCancel={onCancel}
          onOk={onCreate}
          width={360}
          confirmLoading={confirmLoading}
          destroyOnClose={true}
        >
          <Form>
            {getFieldDecorator('version_id', {

                initialValue: model.version_id
              })(<Input type="hidden"/>)}
              <FormItem {...formItemLayout} label={<FormattedMessage {...messages.versionNumber} />}>
                {getFieldDecorator('version_number', {
                  rules: [
                    {
                      required: true,
                      message: <FormattedMessage {...messages.versionNumberRequired} />
                    }
                  ],
                  initialValue: model.version_number
                })(<Input maxLength={maxLength.versionNumber} />)}
              </FormItem>
              <FormItem {...formItemLayout} label={<FormattedMessage {...messages.file} />}>
                {getFieldDecorator('version_file', {
                  rules: [
                    {
                      required: true,
                      message: <FormattedMessage {...messages.fileRequired} />
                    },
                    {
                      validator: this.checkFile
                    }
                  ],
                  initialValue: model.version_file
                })(
                  <UploadFile isEdit={isEdit}/>)}
              </FormItem>
              <FormItem {...formItemLayout} label={<FormattedMessage {...messages.description} />}>
                {getFieldDecorator('description', {
                  initialValue: model.description
                })(<Input maxLength={maxLength.description} />)}
              </FormItem>
          </Form>
        </Modal>
      );
    }
  }
);

export default injectIntl(EditForm);
